import KnTranstion from "../gameui/kn_transtion";
var KnSceneManager = /** @class */ (function () {
    function KnSceneManager(game) {
        this.game = game;
        this.scenes = {};
    }
    // 用户自定义添加场景对象
    KnSceneManager.prototype.addScene = function (id, Stage, boot) {
        console.log("场景已添加 -- ", id);
        var stage = new Stage(this.game, id, boot);
        this.scenes[id] = stage;
        return stage;
    };
    KnSceneManager.prototype.changeScene = function (from, to) {
        var _this = this;
        if (from) {
            // 去除特殊的dat.gui
            from.dat && this.game.gui.remove(from.dat);
            this.game.overlay = new KnTranstion(this.game);
            this.game.overlay.leaveScene(function () {
                from.exit();
                return _this.dispatchScene(to);
            });
        }
        else {
            return this.dispatchScene(to);
        }
    };
    KnSceneManager.prototype.dispatchScene = function (to) {
        var _this = this;
        /** 1.目标场景已缓存
         *  2. 若为加载器场景无需重复进入加载器
         *  3. 目标场景未缓存进入加载器
         * */
        console.log(to);
        if (to.isCached || !to.resources) {
            console.log(to.id, "已缓存");
            // 进入加载好的界面 to 为要进入的界面
            return to.enter();
        }
        else if (to.id === "global_preloader") {
            var globalLoader = this.game.loader;
            // 全局loading界面的资源加载
            globalLoader.filling(to.resources);
            globalLoader.preloader.load(function () {
                console.log("preloadScene resource is ready!");
                _this.scenes["global_preloader"].enter(_this.game.entryHive);
            });
        }
        else {
            // 首次进入场景且场景有资源需要加载，则会自动进入preloader场景
            console.log("首次进入", to.id);
            return this.scenes["global_preloader"].enter(to);
        }
    };
    return KnSceneManager;
}());
export default KnSceneManager;
